import React, { Component } from 'react'
import axios from 'axios'
class GoogleLogin extends Component {
    constructor(props) {
        super(props)
        this.state = {
            calData: []
        }
        this.displayEvents.bind(this);
    }
    displayEvents =  ()=>{
        axios.get('/fetchEvents')
        .then(response => {
            let calData =[...this.state.calData];
            const resData=eval(response.data[0]);
            for(var i in resData.events){
                calData.push({eventName:resData.events[i], recurrence:resData.recurrence[i]})
            }           
            this.setState({calData : calData})
        })
        .catch(err=>console.log("dataE",err))
    }
    render() {
        const {calData} = this.state;
        const eventList = calData.map(cal=><li>{cal.eventName} : {cal.recurrence}</li>)
        return (
            <div>
                <form method="GET" action="/gauth">
                <button onClick={this.getPermission}>Login to Google</button><br/></form>
                <button onClick={this.displayEvents}>Show Google Calendar Events</button>
                <p>Events:</p>
                {eventList}
            </div>
        )
    }
}

export default GoogleLogin
